  .checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgrey;
    padding: 15px 0;
    font-size: 20px;
    align-items: center;
    

    .image-container {
      width: 23%;
      padding-right: 15px;

      img {
        width: 100%;
        height: 100%;
        border-radius: 25px;
      }
    }
    .name,
    .quantity,
    .price {
      width: 23%;
    }

    .quantity {
      display: flex;
      padding-left: 10px;

      .value {
        margin: 0px 10px;
      }

      .arrow {
        cursor: pointer;
      }
    }

    .remove-button {
      padding-left: 12px;
      cursor: pointer;
    }
  }
